<template>
    <div class="cart-bottom-bar">
        <div class="check-content">
        <check-button class='checkBtn' :isChecked='isSelectedAll' @checkBtnClick='selectAll'></check-button>
        <span>全选</span>
        </div>
        <div class='totalPrice'>合计：￥{{totalPrice}}</div>
        <div class='calc'>去计算({{itemCount}})</div>
    </div>
</template>
<script>
import CheckButton from './CheckButton'
import {mapGetters} from 'vuex'
export default {
    name:'CartBottomBar',
    components: {
        CheckButton
    },
    computed: {
        ...mapGetters(['cartList','cartLength']),
        totalPrice() {
            let total = 0;
            for (let item of this.cartList) {
                if(item.checked == true) {
                    total = total + item.price * item.count;
                }
            }
            return total
        },
        itemCount() {
        
            let count = this.cartList.filter(item => {
                return item.checked==true
            })

            return count.length;
        },
        isSelectedAll() {
           if(this,this.cartList.length === 0) {
               return false;
           }
           return !this.cartList.find(item => !item.checked)
        }
    },
    methods: {
        selectAll() {
            if(this.isSelectedAll) {
                this.cartList.forEach(item => item.checked = false);
            }
            else {
                this.cartList.forEach(item => item.checked = true);
            }
        }
    }
}
</script>
<style scoped>
.cart-bottom-bar {
    position: relative;
    display: flex;
    height: 40px;
    
    line-height: 40px;

}
.check-content {
    display: flex;
      width: 60px;
      align-items: center;
      margin-left: 30px;
}

.checkBtn {
    position: absolute;
    line-height: 0;
    left: 12px;
    top: 10px;
}
.totalPrice {
    flex: 1;
}

.calc {
    text-align: center;
    width: 90px;
    background-color: red;
    color: #fff;
}
</style>